import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {BuilderComponent} from './builder/builder.component'
import {FactionSelectComponent} from './faction-select/faction-select.component';
import { GangsummaryComponent } from './gangsummary/gangsummary.component';


const routes: Routes = [
  { path: '', redirectTo: '/faction-select', pathMatch: 'full'},
  { path: 'faction-select', component: FactionSelectComponent },  
  { path: 'builder/:factionId', component: BuilderComponent},
  { path: 'gangsummary/:gang', component: GangsummaryComponent},
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]    
  
})
export class AppRoutingModule { }
