import { CompleteCharacter } from "./completecharacter";
import { Type } from '@angular/core';
import { Equipment } from './equipment';


export class Gang{
characters: CompleteCharacter[];
name: string;
plannedDucats: number;
actualDucats: number;
faction: String;
errorreason: string = "Yes";
isAcceptable: boolean = true;
equipment: Equipment[];


constructor() {
    this.actualDucats = 0;
    this.equipment = [];
}

addCharacter(char: CompleteCharacter){
    this.characters.push(char);
    this.actualDucats = this.actualDucats + char.ducats;
    this.checkAcceptable();
}

getGangCost(): number {
    return this.actualDucats;
}

addEquipment(item: Equipment){
    this.equipment.push(item);
    this.actualDucats = this.actualDucats + item.equipmentCost;
    this.checkAcceptable();
}

removeItem(index: number, item: Equipment){
    this.equipment.splice(index, 1);
    this.actualDucats = this.actualDucats - item.equipmentCost;
    this.checkAcceptable()
}

removeCharacter(index: number,  char: CompleteCharacter){
this.characters.splice(index, 1);
this.actualDucats = this.actualDucats - char.ducats;
this.checkAcceptable();
}

checkAcceptable(){
    this.errorreason = "";
    this.isAcceptable = true;
    this.finderror();
    return this.isAcceptable;
}

getError(): string{
    return this.errorreason;
}

finderror() {
    this.isAcceptable = true;
    this.errorreason = "";
    var leaders: number = 0;    
    var heroes: number = 0;
    var henchmen: number = 0;
    var giftedchar: number = 0;
    var otherfactionchars: number = 0;
    
for(let character of this.characters){
    if (character.charType == "Leader"){
        leaders = leaders +1;
    }
    if (character.charType == "Hero" ){
        heroes = heroes +1;
    }
    if (character.charType == "Henchman"){
        henchmen = henchmen +1;
    }
    if (character.faction == "The Gifted"){
        giftedchar = giftedchar +1;
    }
    if (character.faction != this.faction  && character.faction != "The Gifted"){
        otherfactionchars = otherfactionchars + 1;
    }

}
if (leaders > 1){
    this.isAcceptable = false;    
    var toomanyleaders: string = "Too many Leaders, only one Leader allowed: ";
    this.errorreason = this.errorreason.concat(toomanyleaders);
    
}

if (leaders == 0){
    this.isAcceptable = false;    
    var toofewleaders: string = "Too few Leaders, at least one leader needed: ";
    this.errorreason = this.errorreason.concat(toofewleaders);
}
if (heroes > henchmen){        
        this.isAcceptable = false;        
        var toomanyheroes: string = "More Heroes than Henchman, please remove heroes or add Henchmen: "
        this.errorreason = this.errorreason.concat(toomanyheroes);       
}

if (otherfactionchars > 0){
    this.isAcceptable = false;
    var otherfactionerror: string = "You have selected other factions characters. This is allowed with gifted characters but not others."
    this.errorreason = this.errorreason.concat(otherfactionerror);
}

for (let character of this.characters){
    var checkresult: boolean = false;
    if (character.isUnique == true){        
       
        checkresult = this.checkUniquechar(character);
        if (checkresult == true){
            this.isAcceptable = false;
            
        }
        
        }
        if (checkresult == true){
            var overuseofuniquecharerror: string = "You may only use one "+character.name+ ": ";
            this.errorreason = this.errorreason.concat(overuseofuniquecharerror);
        }
    }
    
this.checkEquipment();


if (this.errorreason == ""){
    this.errorreason = this.errorreason ="Yes";
}
    
}

checkUniquechar(char: CompleteCharacter): boolean{
    var isduplicated: boolean = false;
    var amountofchar: number = 0;
    for (let character of this.characters){
        if (character.charId == char.charId){
            amountofchar = amountofchar + 1;            
        }
    }

    if (amountofchar > 1){
        isduplicated = true;
        
    }


    return isduplicated;

}

checkEquipment():boolean {
var isacceptable: boolean = true;

for (let item of this.equipment){
    var isduplicated: boolean = this.checkDuplicatedItem(item);
    if (isduplicated == true){
        var duplicateitemerror: string = "You can only use one "+item.equipment;
        this.errorreason = this.errorreason.concat(duplicateitemerror);
    }

}

return isacceptable;
}

checkDuplicatedItem(checkItem: Equipment): boolean{
    var isduplicated: boolean = false;
    var numberofitem: number = 0;
    for (let item of this.equipment){
        if (item.equipment == checkItem.equipment){
            numberofitem = numberofitem + 1;            
        }
    }
    if (numberofitem >= 2){
        isduplicated = true;
    }

    return isduplicated
}

}




